import { createRoot } from 'react-dom/client';
import { ReactElement, useState } from 'react';
import {
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    DialogContentText,
} from '@mui/material';

export interface ConfirmProps {
    title?: ReactElement | string;
    content?: ReactElement | string;
    onCancel?: Function;
    onOk?: Function;
}

// TODO 
let isAlert = false;
export function confirm(
    props: ConfirmProps = { title: '', content: '?' }
) {
    if (isAlert) {
        return;
    }
    isAlert = true;
    const promise = new Promise((resolve, reject) => {
        const confirmDialog = (
            <ConfirmDialog
                {...props}
                onCancel={() => {
                    resolve(false);
                    isAlert = false;
                }}
                onOk={() => {
                    resolve(true);
                    isAlert = false;
                }}
            ></ConfirmDialog>
        );
        let dialogElement = document.getElementById('dialog-root');
        if (!dialogElement) {
            dialogElement = document.createElement('div');
            dialogElement.id = 'dialog-root';
            document.getElementsByTagName('body')[0].appendChild(dialogElement);
        }
        createRoot(dialogElement).render(confirmDialog);
    });
    return promise;
}

function ConfirmDialog({
    content,
    title,
    onCancel = () => {},
    onOk = () => {},
}: ConfirmProps) {
    const [isOpen, setIsOpen] = useState(true);
    return (
        <Dialog
            open={isOpen}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    {content}
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button
                    onClick={() => {
                        setIsOpen(false);
                        onCancel();
                    }}
                >
                    
                </Button>
                <Button
                    onClick={() => {
                        setIsOpen(false);
                        onOk();
                    }}
                    autoFocus
                >
                    
                </Button>
            </DialogActions>
        </Dialog>
    );
}
