import GoEasy from 'goeasy';
import { getUid } from '../utils/user';

let status = '';
export const goEasy = GoEasy.getInstance({
    host: 'hangzhou.goeasy.io', //singapore.goeasy.io
    appkey: 'BC-ac1b818e93b84c12a14326fa9a079a30',
    modules: ['pubsub'], //pubsub'im
});
console.log(1111)
export const connect = (): Promise<string> => {
    return new Promise((resolve, reject) => {
        if (status) {
            resolve(status);
        }
        (goEasy as any).connect({
            id: getUid(), //pubsubim60
            data: { id: getUid() }, //pubsubim300
            onSuccess: () => {
                //
                status = 'online';
                resolve('online');
            },
            onFailed: (error) => {
                reject(error);
            },
            onProgress: () => {
                status = '';
            },
        });
    });
};

export const disconnect = () => {};
